.program-selection-list {
  background-color: $bc-color;
  width: 280px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.program-selection-list__item {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: 280px;
  background-color: #f2f2f2;
  margin-bottom: 20px;
}

.program-selection-list__item:last-child {
  margin-bottom: 0;
}

.program-selection-list__image {
  display: block;
  margin-left: 20px;
  background-image: url("../../img/Ellipse\ 1\ copy\ 2.svg");
  background-size: 50px 50px;
  background-repeat: no-repeat;
  background-position: 100%;

  &--pro-size {
    width: 67px;
    height: 94px;
    margin-left: 13px;
    background-position: 100%;
    background-position-x: 9px;
  }

  &--slim-size {
    width: 51px;
    height: 52px;
    margin-left: 19px;
    margin-top: 21px;
  }
}

.program-selection-list__name {
  color: #111111;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin-left: 26px;
  margin-top: 29px;
  margin-bottom: 29px;
}

.program-selection-list__promo {
  width: 240px;
  color: #444444;
  font-family: "Arial Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0 auto;
  padding-bottom: 22px;
  border-bottom: 1px solid #d9d9d9;
}

.program-selection-list__toggle {
  width: 240px;
  height: 16px;
  padding: 0;
  border: none;
  background-image: url("../img/icon-right-arrow-long.svg");
  background-size: 24px 11px;
  background-repeat: no-repeat;
  color: #111111;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  background-position-x: 100px;
  text-align: left;
  margin-left: 19px;
  margin-top: 19px;
  margin-bottom: 23px;
  background-color: transparent;
}

@media (min-width: $tablet-width) and (max-width: $tablet-width-only) {
  .program-selection-list {
    width: 708px;
  }

  .program-selection-list__item {
    width: 708px;
    margin-bottom: 30px;
  }

  .program-selection-list__image {
    width: 200px;
    height: 212px;
    order: 2;
    margin-top: -240px;
    margin-left: 97px;
    background-size: 200px 200px;

    &--pro-size {
      width: 268px;
      height: 283px;
      margin-left: 10px;
      background-position-x: 36px;
    }

    &--slim-size {
      width: 208px;
      height: 253px;
      margin-left: 74px;
    }
  }

  .program-selection-list__name {
    width: 370px;
    font-size: 36px;
    line-height: 37px;
    order: -1;
    margin-left: 53px;
    margin-top: 76px;
    margin-bottom: 25px;
  }

  .program-selection-list__promo {
    width: 344px;
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 0;
    border-bottom: none;
    order: 0;
    margin-left: 52px;
    margin-bottom: 56px;
  }

  .program-selection-list__toggle {
    width: 350px;
    height: 20px;
    background-position-x: 123px;
    background-position-y: 4px;
    font-size: 20px;
    line-height: 20px;
    order: 1;
    margin-left: 52px;
    margin-bottom: 62px;
    margin-top: 0;
  }
}

@media (min-width: $desktop-width) {
  .program-selection-list {
    width: 1220px;
    display: flex;
    margin-top: 637px;
  }

  .program-selection-list__item {
    width: 570px;
    margin-right: 80px;
    margin-bottom: 0;
  }

  .program-selection-list__item:last-child {
    margin-right: 0;
  }

  .program-selection-list__name {
    width: 250px;
    font-size: 36px;
    margin-top: 77px;
    margin-bottom: 0;
    margin-left: 0;
  }

  .program-selection-list__promo {
    width: 462px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
    margin: 0;
    margin-left: 52px;
    margin-top: 43px;
  }

  .program-selection-list__toggle {
    height: 20px;
    font-size: 20px;
    line-height: 20px;
    margin: 0;
    background-position-x: 126px;
    background-position-y: 5px;
    margin-left: 52px;
    margin-bottom: 64px;
    margin-top: 5px;
  }

  .program-selection-list__image {
    margin-left: 50px;
    width: 100px;
    height: 105px;
    margin-top: 40px;
    margin-right: 65px;
    background-size: 100px 100px;

    &--pro-size {
      width: 134px;
      height: 117px;
      margin-left: 34px;
      background-position-x: 18px;
    }

    &--slim-size {
      width: 101px;
      height: 111px;
      margin-left: 50px;
      background-position-y: 8px;
    }
  }
}
