.example {
  width: 100%;
  background-color: #f2f2f2;
  margin: 0 auto;
}

.example__title {
  font-family: "Oswaldregular", sans-serif;
  font-size: 36px;
  color: #111111;
  line-height: 60px;
  margin: 0;
  padding: 0;
  padding-top: 25px;
  margin-left: 20px;
}

.example__text {
  font-family: "Arial Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0;
  margin-top: 40px;
  margin-left: 20px;
}

.example__cost {
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 14px;
  color: #111111;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

.example__before-text {
  color: #111111;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.example__after-text {
  color: #111111;
  font-family: "Oswaldregular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
